#now__sidepanel {
  height: 100vh;
  width: 30%; /* 30% width on large screens */
  position: fixed;
  z-index: 1000;
  top: 0;
  left: -30%; /* Hide side panel to the left */
  overflow-x: hidden;
  transition: left 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

#now__sidepanel.open {
  left: 0;
}

/* Medium screens */
@media screen and (max-width: 1200px) {
  #now__sidepanel {
    width: 50%; /* 50% width on medium screens */
    left: -50%; /* Adjust left position */
  }
}

/* Small and extra small screens */
@media screen and (max-width: 768px) {
  #now__sidepanel {
    width: 80%; /* 80% width on small screens */
    left: -80%; /* Adjust left position */
  }
}

#now__sidepanel a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

#now__sidepanel a:hover {
  color: #f1f1f1;
}

.closebtn {
  position: absolute;
  top: 20px;
  right: 25px;
  font-size: 36px;
  cursor: pointer;
  color: #818181;
  transition: color 0.3s;
}

.closebtn:hover {
  color: #f1f1f1;
}

/* Page wrapper styles */
#base__wrapper {
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transform: translateX(0);
  width: 100%;
  position: relative;
  left: 0;
}

#now-button-container {
  position: fixed;
  right: 20%;
  top: 20%;
  z-index: 999;
}

#base__wrapper.slide-right {
  transform: translateX(50%);
}

@media screen and (max-width: 768px) {
  #now__sidepanel {
    width: 80%;
    left: -80%;
  }
  #base__wrapper {
    transform: none !important;
  }
}
@media screen and (max-width: 992px) {
  #now__sidepanel {
    width: 50%;
    left: -50%;
  }
  #base__wrapper {
    transform: none !important;
